                            letter-spacing: 1px;
                            font-weight: 800;
                            text-transform: uppercase;
                            line-height: 1.2;
                            border: 0;
                            outline: 0;
                            background: #fff;
                            border: 2px solid #6A7686;
                            color: #6A7686;
                            text-shadow: none;
                            border-radius: 4px;
                            &:hover {
                                background: #0085CC;
                                color: #fff;
                                border-color: #0085cc;
                            }
                        }
                        textarea {
                            width: 100%;
                            display: block;
                            -moz-box-sizing: border-box;
                            -webkit-box-sizing: border-box;
                            -ms-box-sizing: border-box;
                            box-sizing: border-box;
