                        padding: 0;
                        margin: 2em 0 6em 0;
                        list-style: none;
                        li {
                            display: inline-block;
                            padding: 0;
                            margin-bottom: -3px;
                            a {
                                display: block;
                                float: left;
                                margin: 0 .4em .3em 0;
                                text-align: center;
                                font-size: 20px;
                                width: 42px;
                                line-height: 40px;
                                color: #516075;
                                border-radius: 4px;
                                border: 1px solid #dde4e6;
                                text-decoration: none;
                            }
                            .fa-facebook {
